import React, { Component } from 'react';
export default class About extends Component {
  render() {
    let aboutData = this.props.aboutData;
    const backEnd = aboutData.skillStack.filter(item => item.category.includes('backEnd'));
    const frontEnd = aboutData.skillStack.filter(item => item.category.includes('frontEnd'));
    const etc = aboutData.skillStack.filter(item => item.category.includes('etc'));
    const experience = aboutData.historys.filter(item => item.category.includes('experience'));
    const license = aboutData.historys.filter(item => item.category.includes('license'));
    return (
      <section id="about">
         <div className="row">
            <div className="three columns">
               <img className="profile-pic" src={aboutData.meImageUrl} alt="" />
            </div>
            <div className="nine columns main-col">
               <div className="about-start">
                  <h2><span>About Me</span></h2>
               </div>
               <div className="about-block">
                  <h3><span>*저는 이런 개발자 입니다</span></h3>
                  <p>{aboutData.introduction}</p>
               </div>
               <div className="about-block">
                  <h3><span>*기술 스택</span></h3>
                  <div className="contentsLeft">
                     <h4><span>-BackEnd</span></h4>
                     {              
                        backEnd && backEnd.map((item) => {
                           return(
                              <div key={item.contents} className="contents">
                                 <span>&bull;</span>{item.contents}
                              </div>
                           )
                        })
                     }
                  </div>
                  <div className="contentsRight">
                     <h4><span>-FrontEnd</span></h4>
                     {                
                        frontEnd && frontEnd.map((item) => {
                           return(
                              <div key={item.contents} className="contents">
                                 <span>&bull;</span>{item.contents}
                              </div>
                           )
                        })
                     }
                  </div>
                  <div className="contentsLeft">
                     <h4><span>-Etc..</span></h4>
                     {                
                        etc && etc.map((item) => {
                           return(
                              <div key={item.contents} className="contents">
                                 <span>&bull;</span>{item.contents}
                              </div>
                           )
                        })
                     }
                  </div>  
               </div>
               <div className="about-block">
                  <div className="contentsLeft">
                     <h3><span>*학력, 활동</span></h3>
                     {                
                        experience && experience.map((item) => {
                           return(
                              <div key={item.contents} className="contents">
                                 <span>&bull;</span>{item.contents}
                              </div>
                           )
                        })
                     }
                  </div>
                  <div className="contentsRight">
                     <h3><span>*자격증</span></h3>
                     {
                        license && license.map((item) => {
                           return(
                              <div key={item.contents} className="contents">
                                 <span>&bull;</span>{item.contents}
                              </div>
                           )
                        })
                     }
                  </div>
               </div>
               <div>
                  <h5>Favorite: {aboutData.favorite}</h5>
                  <a href={aboutData.resumeDownload} download="resume.pdf">
                     <button className="download"><i>resume download</i></button>
                  </a>
                  <a href={aboutData.portfolioDownload} download="portfolio.pdf">
                     <button className="download"><i>portfolio download</i></button>
                  </a>
               </div>
            </div>
         </div>
      </section>
    );
  }
}